import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { RouterProps } from '@reach/router';

import UserPageLayout from '../../../common/components/layout/UserPageLayout';
import FormInput, { FormType } from '../../../common/components/form/FormInput';
import { loginMember } from '../../user/forces/actions';
import { RootState } from '../../../root.reducer';
import { withFormik, FormikProps, Form, Field, FieldProps, FormikBag } from 'formik';
import { formFields, FormValues, validation } from './fields/loginFields';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import FormAlert from '../../../common/components/form/FormAlert';
import FormInputError from '../../../common/components/form/FormInputError';
import Link from '../../../common/components/link/Link';
import Routes from '../../../common/routing/Routes';
import LoginMessage from '../components/LoginMessage';
import styles from './Login.module.css';

type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type Props = {} & React.HTMLAttributes<HTMLElement> & DispatchProps & RouterProps;
type PropsWithFormik = Props & React.HTMLAttributes<HTMLElement> & FormikProps<FormValues>;

class Login extends React.Component<PropsWithFormik> {
  render() {
    const { className, location, ...props } = this.props;
    return (
      <UserPageLayout title="Logg inn" {...props}>
        {location && location.state && location.state.message && (
          <LoginMessage message={location.state.message} />
        )}
        <Form>
          {formFields.map(formField => (
            <React.Fragment key={formField.name}>
              <Field
                name={formField.name}
                render={({ field }: FieldProps<FormValues>) => (
                  <FormInput
                    {...field}
                    styleType="secondary"
                    type={formField.type}
                    required={formField.required}
                    placeholder={formField.placeholder}
                  />
                )}
              />
              <FormInputError name={formField.name} component="div" />
            </React.Fragment>
          ))}
          <FormAlert error={this.props.status} />
          {this.props.isSubmitting ? (
            <LoadingIndicator />
          ) : (
            <div className={styles.buttonContainer}>
              <div className={styles.link}>
                <Link
                  underlined
                  type="secondary"
                  to={Routes.Register()}
                  className={styles.memberLink}>
                  Bli medlem!
                </Link>
                <Link underlined type="secondary" to={Routes.ForgotPassword()}>
                  Glemt passord?
                </Link>
              </div>
              <CallToActionButton
                className={styles.loginButton}
                type="submit"
                disabled={this.props.isSubmitting || !this.props.isValid}>
                Logg inn
              </CallToActionButton>
            </div>
          )}
        </Form>
      </UserPageLayout>
    );
  }
}

const formOptions = {
  mapPropsToValues: (props: Props) => ({
    phoneNumber: '',
    password: '',
  }),
  handleSubmit: async (values: FormValues, formikBag: FormikBag<Props, FormValues>) => {
    try {
      await formikBag.props.loginMember(values);
      formikBag.setSubmitting(false);
    } catch (err) {
      formikBag.setStatus(err.message || 'Something went wrong');
      formikBag.setSubmitting(false);
    }
  },
  validationSchema: validation,
};
const FormikLogin = withFormik<Props, FormValues>(formOptions)(Login);

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      loginMember,
    },
    dispatch
  );

export default connect<RootState>(
  null,
  mapDispatchToProps
)(FormikLogin);
