import * as Yup from 'yup';

export interface FormValues {
  phoneNumber: string;
  password: string;
}

export const formFields = [
  {
    name: 'phoneNumber',
    type: 'tel',
    placeholder: 'Phone number',
    error: 'Skriv',
    required: true,
  },
  {
    name: 'password',
    type: 'password',
    placeholder: 'Password',
    error: 'Skriv',
    required: true,
  },
];

export const validation = Yup.object().shape<FormValues>({
  phoneNumber: Yup.string()
    .min(8, 'Too Short!')
    .max(12, 'Too Long!'),
  password: Yup.string()
    .min(2, 'Too Short!')
    .max(50, 'Too Long!'),
});
